.icon-help {
  background: url('../../images/icons/help.png') 0 50% no-repeat;
  padding: 1px 0 1px 20px;
}

[dir='rtl'] {
  .icon-help {
    background-position: 100% 50%;
    padding: 1px 20px 1px 0;
  }
}

.feed-icon {
  display: block;
  background: url('../../images/icons/feed.svg') no-repeat;
  width: 16px;
  height: 16px;
  overflow: hidden;
  text-indent: -9999px;
}
